import React from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import Paper from "@material-ui/core/Paper";
// @ts-ignore
import doh from 'dohjs'
import Button from '@material-ui/core/Button';
import Link from "@material-ui/core/Link";

interface answer {
    name: string
    type: string
    ttl: number
    class: string
    flush: boolean
    data: string
}

const App: React.FunctionComponent = () => {
    const [result, setResult] = React.useState<string[]>(["# DLSITE"])
    const loadContent = () => {
        setResult(["# DLSITE"])
        const DohPub = new doh.DohResolver("https://doh.pub/dns-query")
        const Cloudflare = new doh.DohResolver("https://cloudflare-dns.com/dns-query")

        const hosts = [
            ["trial.dlsite.com", "img.dlsite.jp", "media.dlsite.com", "play.dl.dlsite.com", "media.ci-en.jp"
                , "media.stg.ci-en.jp", "www.nijiyome.com", "www.nijiyome.jp", "file.chobit.cc", "img.chobit.cc"],
            ["ssl.dlsite.com", "www.dlsite.com"],
            ["play.dlsite.com"],
            ["login.dlsite.com"],
            ["download.dlsite.com"]
        ]

        const hostMap = new Map<[string, any], string[]>([
            [["download.sangfor.com.cn", DohPub], hosts[0]],
            [["w2.shared.global.fastly.net", Cloudflare], hosts[1]],
            [["play.dlsite.com", Cloudflare], hosts[2]],
            [["login.dlsite.com", Cloudflare], hosts[3]],
            [["download.dlsite.com.wtxcdn.com", DohPub], hosts[4]]
        ])

        hostMap.forEach((value, key) => {
            key[1].query(key[0], "A", "GET").then((response: { answers: answer[]; }) => {
                const ip_addr = response.answers
                    .filter(ans => ans.type === "A")
                    .map(ans => ans.data)
                const list = [`# ${key[0]}`].concat(value.flatMap(host => ip_addr.map(ip => `${ip} ${host} `)))
                setResult(prevState => prevState.concat(list))
            })
        })
    }

    React.useEffect(loadContent, [])

    return (
        <React.Fragment>
            <CssBaseline />
            <Button onClick={loadContent} color="primary">刷新</Button>
            <Link href="https://github.com/cssxsh/dlsite-downloader/releases">DLISTE 多线程下载器 无需梯子</Link>
            <Paper>
                {result.map(line => (<li key={line}>{line}</li>))}
            </Paper>
        </React.Fragment>
    );
}

export default App;
